import React from "react";
import { AbsoluteFill, useVideoConfig, Sequence } from "remotion";
import { Subtitles, episode04Subtitles } from "../components/Subtitles";
import { Audio } from "../components/Audio";
import { getEpisodeBGM } from "../utils/audioConfig";
import {
  OpeningWilsonScene,
  JekyllIslandScene,
  SevenTycoonsScene,
  MorganFamilyScene,
  RockefellerScene,
  Panic1907Scene,
  GoldToFiatScene,
  Election1912Scene,
  PlanBScene,
  ActPassesScene,
  WhoOwnsFedScene,
  HiddenControlScene,
  WilsonRealizationScene,
  SummaryScene,
} from "../components/scenes/episode04";

/**
 * Episode04 - 4
 *
 * 
 * 
 *
 * 12-14720-840 = 21600-25200 @ 30fps
 *
 * 
 * 0. [0-35s]  - 
 * 1. [35-95s] 
 * 2. [95-170s] 7
 * 3. [170-240s] 
 * 4. [240-310s] 
 * 5. [310-370s] 1907 - 
 * 6. [370-430s] 
 * 7. [430-490s] 1912
 * 8. [490-550s] B - 
 * 9. [550-630s] 
 * 10. [630-700s] 
 * 11. [700-760s] 
 * 12. [760-810s] 
 * 13. [810-840s] 
 */
export const Episode04: React.FC = () => {
  const { fps } = useVideoConfig();

  const bgm = getEpisodeBGM("Episode04");

  return (
    <AbsoluteFill style={{ background: "#0d1117" }}>
      {bgm && <Audio {...bgm} />}
      {/* Scene 0:  -  (0-35s =  0-1050) */}
      <Sequence durationInFrames={35 * fps}>
        <OpeningWilsonScene />
      </Sequence>

      {/* Scene 1:  (35-95s =  1050-2850) */}
      <Sequence from={35 * fps} durationInFrames={60 * fps}>
        <JekyllIslandScene />
      </Sequence>

      {/* Scene 2: 7 (95-170s =  2850-5100) */}
      <Sequence from={95 * fps} durationInFrames={75 * fps}>
        <SevenTycoonsScene />
      </Sequence>

      {/* Scene 3:  (170-240s =  5100-7200) */}
      <Sequence from={170 * fps} durationInFrames={70 * fps}>
        <MorganFamilyScene />
      </Sequence>

      {/* Scene 4:  (240-310s =  7200-9300) */}
      <Sequence from={240 * fps} durationInFrames={70 * fps}>
        <RockefellerScene />
      </Sequence>

      {/* Scene 5: 1907 -  (310-370s =  9300-11100) */}
      <Sequence from={310 * fps} durationInFrames={60 * fps}>
        <Panic1907Scene />
      </Sequence>

      {/* Scene 6:  (370-430s =  11100-12900) */}
      <Sequence from={370 * fps} durationInFrames={60 * fps}>
        <GoldToFiatScene />
      </Sequence>

      {/* Scene 7: 1912 (430-490s =  12900-14700) */}
      <Sequence from={430 * fps} durationInFrames={60 * fps}>
        <Election1912Scene />
      </Sequence>

      {/* Scene 8: B -  (490-550s =  14700-16500) */}
      <Sequence from={490 * fps} durationInFrames={60 * fps}>
        <PlanBScene />
      </Sequence>

      {/* Scene 9:  (550-630s =  16500-18900) */}
      <Sequence from={550 * fps} durationInFrames={80 * fps}>
        <ActPassesScene />
      </Sequence>

      {/* Scene 10:  (630-700s =  18900-21000) */}
      <Sequence from={630 * fps} durationInFrames={70 * fps}>
        <WhoOwnsFedScene />
      </Sequence>

      {/* Scene 11:  (700-760s =  21000-22800) */}
      <Sequence from={700 * fps} durationInFrames={60 * fps}>
        <HiddenControlScene />
      </Sequence>

      {/* Scene 12:  (760-810s =  22800-24300) */}
      <Sequence from={760 * fps} durationInFrames={50 * fps}>
        <WilsonRealizationScene />
      </Sequence>

      {/* Scene 13:  (810-840s =  24300-25200) */}
      <Sequence from={810 * fps} durationInFrames={30 * fps}>
        <SummaryScene />
      </Sequence>

      {/*  -  */}
      <Subtitles subtitles={episode04Subtitles} />
    </AbsoluteFill>
  );
};

// 
export default Episode04;
