import React from "react";
import { useCurrentFrame, interpolate } from "remotion";

/**
 * AnimatedTransition - 
 *
 * framer-motion
 * 
 */
interface AnimatedTransitionProps {
  children: React.ReactNode;
  delay?: number;
  duration?: number;
  type?: "fade" | "scale" | "slide" | "bounce";
  direction?: "up" | "down" | "left" | "right";
}

export const AnimatedTransition: React.FC<AnimatedTransitionProps> = ({
  children,
  delay = 0,
  duration = 30,
  type = "fade",
  direction = "up",
}) => {
  const frame = useCurrentFrame();

  // 
  const progress = interpolate(frame, [delay, delay + duration], [0, 1], {
    extrapolateRight: "clamp",
  });

  const getTransform = () => {
    switch (type) {
      case "fade":
        return { opacity: progress };
      case "scale":
        return {
          opacity: progress,
          transform: `scale(${0.5 + progress * 0.5})`,
        };
      case "slide": {
        const offset = 100 * (1 - progress);
        const position =
          direction === "up"
            ? `translateY(${offset}px)`
            : direction === "down"
              ? `translateY(${-offset}px)`
              : direction === "left"
                ? `translateX(${offset}px)`
                : `translateX(${-offset}px)`;
        return {
          opacity: progress,
          transform: position,
        };
      }
      case "bounce": {
        const bounceProgress =
          progress < 0.5 ? progress * 2 * 1.2 : 1 - (1 - progress) * 2 * 0.2;
        return {
          opacity: progress,
          transform: `scale(${bounceProgress})`,
        };
      }
      default:
        return { opacity: progress };
    }
  };

  return <div style={getTransform()}>{children}</div>;
};

/**
 * StaggerTransition - 
 *
 * 
 */
interface StaggerTransitionProps {
  children: React.ReactNode[];
  staggerDelay?: number;
  itemDelay?: number;
  type?: "fade" | "scale" | "slide";
}

export const StaggerTransition: React.FC<StaggerTransitionProps> = ({
  children,
  staggerDelay = 5,
  itemDelay = 10,
  type = "fade",
}) => {
  const frame = useCurrentFrame();

  return (
    <>
      {React.Children.map(children, (child, index) => {
        const delay = staggerDelay + index * itemDelay;
        const progress = interpolate(frame, [delay, delay + 30], [0, 1], {
          extrapolateRight: "clamp",
        });

        const getTransform = () => {
          switch (type) {
            case "fade":
              return { opacity: progress };
            case "scale":
              return {
                opacity: progress,
                transform: `scale(${0.5 + progress * 0.5})`,
              };
            case "slide":
              return {
                opacity: progress,
                transform: `translateY(${(1 - progress) * 50}px)`,
              };
            default:
              return { opacity: progress };
          }
        };

        return <div style={getTransform()}>{child}</div>;
      })}
    </>
  );
};

/**
 * ZoomInTransition - 
 * 
 */
export const ZoomInTransition: React.FC<{
  children: React.ReactNode;
  delay?: number;
  duration?: number;
}> = ({ children, delay = 0, duration = 30 }) => {
  return (
    <AnimatedTransition type="scale" delay={delay} duration={duration}>
      {children}
    </AnimatedTransition>
  );
};

/**
 * FadeInTransition - 
 * 
 */
export const FadeInTransition: React.FC<{
  children: React.ReactNode;
  delay?: number;
  duration?: number;
}> = ({ children, delay = 0, duration = 30 }) => {
  return (
    <AnimatedTransition type="fade" delay={delay} duration={duration}>
      {children}
    </AnimatedTransition>
  );
};

/**
 * SlideInTransition - 
 * 
 */
export const SlideInTransition: React.FC<{
  children: React.ReactNode;
  delay?: number;
  duration?: number;
  direction?: "up" | "down" | "left" | "right";
}> = ({ children, delay = 0, duration = 30, direction = "up" }) => {
  return (
    <AnimatedTransition
      type="slide"
      delay={delay}
      duration={duration}
      direction={direction}
    >
      {children}
    </AnimatedTransition>
  );
};

/**
 * ParallaxLayer - 
 *
 * 
 */
export const ParallaxLayer: React.FC<{
  children: React.ReactNode;
  speed?: number;
  offset?: number;
}> = ({ children, speed = 1, offset = 0 }) => {
  const frame = useCurrentFrame();

  const y = interpolate(frame, [0, 120], [offset, offset + speed * 100]);

  return (
    <div
      style={{
        position: "absolute",
        transform: `translateY(${y}px)`,
        willChange: "transform",
      }}
    >
      {children}
    </div>
  );
};
