import React from "react";
import { AbsoluteFill, useVideoConfig, Sequence, useCurrentFrame, interpolate } from "remotion";
import { Audio } from "../components/Audio";
import { Subtitles } from "../components/Subtitles";
import { getEpisodeBGM } from "../utils/audioConfig";
import { trailerSubtitles } from "../subtitles/trailer";

/**
 * Trailer - 
 *
 * 752250 @30fps
 */
export const Trailer: React.FC = () => {
  const { fps } = useVideoConfig();

  const bgm = getEpisodeBGM("Episode01");

  return (
    <AbsoluteFill style={{ background: "#0d1117" }}>
      {bgm && <Audio {...bgm} />}

      {/* 1:  (0-15s) */}
      <Sequence durationInFrames={15 * fps}>
        <OpeningScene />
      </Sequence>

      {/* 2:  (15-30s) */}
      <Sequence from={15 * fps} durationInFrames={15 * fps}>
        <QuestionScene />
      </Sequence>

      {/* 3:  (30-45s) */}
      <Sequence from={30 * fps} durationInFrames={15 * fps}>
        <CrisisScene />
      </Sequence>

      {/* 4:  (45-60s) */}
      <Sequence from={45 * fps} durationInFrames={15 * fps}>
        <ChinaScene />
      </Sequence>

      {/* 5:  (60-75s) */}
      <Sequence from={60 * fps} durationInFrames={15 * fps}>
        <ComingSoonScene />
      </Sequence>

      <Subtitles subtitles={trailerSubtitles} />
    </AbsoluteFill>
  );
};

const OpeningScene: React.FC = () => {
  const frame = useCurrentFrame();
  const opacity = interpolate(frame, [0, 30], [0, 1]);

  return (
    <AbsoluteFill style={{ background: "linear-gradient(180deg, #0d1117 0%, #1a1a2e 100%)" }}>
      <div style={{ position: "absolute", top: "35%", left: "50%", transform: "translate(-50%, -50%)", textAlign: "center", opacity }}>
        <div style={{ fontSize: 72, fontWeight: 700, color: "#ffd700", textShadow: "0 0 30px rgba(255,215,0,0.5)" }}>Currency Wars</div>
        <div style={{ fontSize: 36, color: "#e8e8e8", marginTop: 20 }}></div>
        <div style={{ fontSize: 24, color: "#ef4444", marginTop: 40, fontStyle: "italic" }}>""</div>
      </div>
    </AbsoluteFill>
  );
};

const QuestionScene: React.FC = () => {
  const frame = useCurrentFrame();
  const opacity = interpolate(frame, [0, 30], [0, 1]);

  return (
    <AbsoluteFill style={{ background: "#0d1117" }}>
      <div style={{ position: "absolute", top: "30%", left: "50%", transform: "translate(-50%, -50%)", width: "80%", textAlign: "center", opacity }}>
        <div style={{ fontSize: 28, color: "#ffd700", marginBottom: 30 }}></div>
        <div style={{ fontSize: 20, color: "#9ca3af", marginBottom: 20 }}></div>
        <div style={{ fontSize: 20, color: "#9ca3af" }}></div>
      </div>
    </AbsoluteFill>
  );
};

const CrisisScene: React.FC = () => {
  const frame = useCurrentFrame();
  const opacity = interpolate(frame, [0, 30], [0, 1]);

  return (
    <AbsoluteFill style={{ background: "linear-gradient(180deg, #1f1f1f 0%, #0d0d0d 100%)" }}>
      <div style={{ position: "absolute", top: "25%", left: "50%", transform: "translate(-50%, -50%)", width: "80%", textAlign: "center", opacity }}>
        <div style={{ fontSize: 48, color: "#ef4444", fontWeight: 700, marginBottom: 20 }}>2008 Global Financial Crisis</div>
        <div style={{ fontSize: 24, color: "#e8e8e8", marginBottom: 15 }}></div>
        <div style={{ fontSize: 24, color: "#e8e8e8", marginBottom: 15 }}></div>
        <div style={{ fontSize: 20, color: "#9ca3af" }}></div>
      </div>
    </AbsoluteFill>
  );
};

const ChinaScene: React.FC = () => {
  const frame = useCurrentFrame();
  const opacity = interpolate(frame, [0, 30], [0, 1]);

  return (
    <AbsoluteFill style={{ background: "linear-gradient(180deg, #0d1117 0%, #1a2f1a 100%)" }}>
      <div style={{ position: "absolute", top: "30%", left: "50%", transform: "translate(-50%, -50%)", width: "80%", textAlign: "center", opacity }}>
        <div style={{ fontSize: 36, color: "#ffd700", marginBottom: 30 }}></div>
        <div style={{ fontSize: 28, color: "#e8e8e8", marginBottom: 20 }}></div>
        <div style={{ fontSize: 20, color: "#ef4444", marginTop: 30 }}></div>
      </div>
    </AbsoluteFill>
  );
};

const ComingSoonScene: React.FC = () => {
  const frame = useCurrentFrame();
  const opacity = interpolate(frame, [0, 30], [0, 1]);
  const footerOpacity = interpolate(frame, [60, 90], [0, 1]);

  return (
    <AbsoluteFill style={{ background: "#0d1117" }}>
      <div style={{ position: "absolute", top: "25%", left: "50%", transform: "translate(-50%, -50%)", width: "80%", textAlign: "center", opacity }}>
        <div style={{ fontSize: 32, color: "#e8e8e8", marginBottom: 30 }}>11</div>
        <div style={{ display: "flex", justifyContent: "center", gap: 20, flexWrap: "wrap" }}>
          {["", "", "", "", ""].map((item, i) => (
            <div key={i} style={{ padding: "10px 20px", background: "rgba(255,215,0,0.15)", borderRadius: 8, border: "1px solid #ffd700", color: "#ffd700", fontSize: 16 }}>{item}</div>
          ))}
        </div>
      </div>
      <div style={{ position: "absolute", bottom: "20%", left: "50%", transform: "translateX(-50%)", textAlign: "center", opacity: footerOpacity }}>
        <div style={{ fontSize: 48, color: "#ffd700", fontWeight: 700 }}>Coming Soon</div>
        <div style={{ fontSize: 24, color: "#9ca3af", marginTop: 10 }}></div>
      </div>
    </AbsoluteFill>
  );
};

export default Trailer;
