import React from "react";
import { AbsoluteFill, useVideoConfig, Sequence } from "remotion";
import { Subtitles, episode05Subtitles } from "../components/Subtitles";
import { Audio } from "../components/Audio";
import { getEpisodeBGM } from "../utils/audioConfig";
import {
  BenjaminStrongScene,
  NoFedNoWarScene,
  StrongWartimeFedScene,
  WilsonWarDecisionScene,
  WarProfiteeringScene,
  VersaillesTreatyScene,
  AgriculturalDepression1921Scene,
  RoaringTwentiesScene,
  Conspiracy1927Scene,
  BubbleInflationScene,
  Crash1929Scene,
  FireSaleScene,
  DepressionScene,
  RealPlotScene,
  NewDealScene,
  Episode05SummaryScene,
} from "../components/scenes/episode05";

/**
 * Episode05 - 5""
 *
 * 
 * 1914-1939
 *
 * 15900 = 27,000 @ 30fps
 *
 * 
 * 0. [0-40s]  -  (1,200)
 * 1. [40-100s]  (3,000)
 * 2. [100-160s]  (1,800)
 * 3. [160-220s]  (1,800)
 * 4. [220-280s]  (1,800)
 * 5. [280-340s]  (1,800)
 * 6. [340-410s] 1921 (2,100)
 * 7. [410-470s]  (1,800)
 * 8. [470-530s] 1927 (1,800)
 * 9. [530-600s]  (2,100)
 * 10. [600-680s] 1929 (2,400)
 * 11. [680-750s]  (2,100)
 * 12. [750-820s]  (2,100)
 * 13. [820-880s]  (1,800)
 * 14. [880-940s]  (1,800)
 * 15. [940-900s]  (1,800)
 */
export const Episode05: React.FC = () => {
  const { fps } = useVideoConfig();

  const bgm = getEpisodeBGM("Episode05");

  return (
    <AbsoluteFill style={{ background: "#0d1117" }}>
      {bgm && <Audio {...bgm} />}
      {/* Scene 0:  -  (0-40s =  0-1200) */}
      <Sequence durationInFrames={40 * fps}>
        <BenjaminStrongScene />
      </Sequence>

      {/* Scene 1:  (40-100s =  1200-3000) */}
      <Sequence from={40 * fps} durationInFrames={60 * fps}>
        <NoFedNoWarScene />
      </Sequence>

      {/* Scene 2:  (100-160s =  3000-4800) */}
      <Sequence from={100 * fps} durationInFrames={60 * fps}>
        <StrongWartimeFedScene />
      </Sequence>

      {/* Scene 3:  (160-220s =  4800-6600) */}
      <Sequence from={160 * fps} durationInFrames={60 * fps}>
        <WilsonWarDecisionScene />
      </Sequence>

      {/* Scene 4:  (220-280s =  6600-8400) */}
      <Sequence from={220 * fps} durationInFrames={60 * fps}>
        <WarProfiteeringScene />
      </Sequence>

      {/* Scene 5:  (280-340s =  8400-10200) */}
      <Sequence from={280 * fps} durationInFrames={60 * fps}>
        <VersaillesTreatyScene />
      </Sequence>

      {/* Scene 6: 1921 (340-410s =  10200-12300) */}
      <Sequence from={340 * fps} durationInFrames={70 * fps}>
        <AgriculturalDepression1921Scene />
      </Sequence>

      {/* Scene 7:  (410-470s =  12300-14100) */}
      <Sequence from={410 * fps} durationInFrames={60 * fps}>
        <RoaringTwentiesScene />
      </Sequence>

      {/* Scene 8: 1927 (470-530s =  14100-15900) */}
      <Sequence from={470 * fps} durationInFrames={60 * fps}>
        <Conspiracy1927Scene />
      </Sequence>

      {/* Scene 9:  (530-600s =  15900-18000) */}
      <Sequence from={530 * fps} durationInFrames={70 * fps}>
        <BubbleInflationScene />
      </Sequence>

      {/* Scene 10: 1929 (600-680s =  18000-20400) */}
      <Sequence from={600 * fps} durationInFrames={80 * fps}>
        <Crash1929Scene />
      </Sequence>

      {/* Scene 11:  (680-750s =  20400-22500) */}
      <Sequence from={680 * fps} durationInFrames={70 * fps}>
        <FireSaleScene />
      </Sequence>

      {/* Scene 12:  (750-820s =  22500-24600) */}
      <Sequence from={750 * fps} durationInFrames={70 * fps}>
        <DepressionScene />
      </Sequence>

      {/* Scene 13:  (820-880s =  24600-26400) */}
      <Sequence from={820 * fps} durationInFrames={60 * fps}>
        <RealPlotScene />
      </Sequence>

      {/* Scene 14:  (880-940s =  26400-28200) */}
      <Sequence from={880 * fps} durationInFrames={60 * fps}>
        <NewDealScene />
      </Sequence>

      {/* Scene 15:  (940-900s =  28200-27000) */}
      <Sequence from={940 * fps} durationInFrames={60 * fps}>
        <Episode05SummaryScene />
      </Sequence>

      {/*  -  */}
      <Subtitles subtitles={episode05Subtitles} />
    </AbsoluteFill>
  );
};

// 
export default Episode05;
